* {
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}
body {
  padding: 40px;
}
input {
  width: 100%;
  font-size: 1.2rem;
  color: grey;
}
.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.card {
align-content: center;
border: 1px solid grey;
border-radius: 6px;
overflow: hidden;
margin-right: 10px;
margin-bottom: 10px;
}
.card p {
    padding:5px;
    text-align: center;
}
.card img {
  max-height: 170px;
}
